$checked: rgb(149, 240, 187);
$checking: rgb(247, 179, 179);
$inProgress: rgb(248, 247, 171);
$todo: rgb(216, 216, 216);
$notMark: #8f3535;
$student: rgb(245, 238, 142);
$notseen: white;
$notmade: rgb(186, 160, 248);

.Table {
  padding-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: left;


  .col {
    border: 1px solid;
    padding: 0;
  }

  div {
    overflow: auto;
    display: block;
    min-width: 235px;

    table {
      font-size: 14px;
      border-collapse: separate;
      text-align: center;

      td,
      th {
        font-size: 16px;
        margin: 0;
        padding: 5px;
        white-space: nowrap;
        border-radius: 5px;

        a {
          text-decoration: none;
          color: black;

          &:hover {
            color: rgb(44, 26, 2);
            text-shadow: 2px 2px 2px gray;
          }

          &:active {
            text-shadow: 1px 1px 1px gray;
          }
        }
      }
    }
  }

  .Checked {
    background: $checked;
    color: $checked;
  }

  .Checking {
    background: $checking;
  }

  .CheckNow {
    background: $checking;
    color: $checking;
  }

  .InProgress {
    background: $inProgress;
  }

  .Progress {
    background: $inProgress;
    color: $inProgress;
  }

  .ToDo {
    background: $todo;
  }

  .ToDoNow {
    background: $todo;
    color: $todo;
  }

  .NotChecked {
    background: $notMark;
    color: $notMark;
  }

  .Student {
    background: $student;
  }

  .notSeen {
    color: $notseen;
  }

  .NotMade {
    background: $notmade;
    color: $notmade;
  }
}
