header {
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  color: white;
  .Header-logo {
    height: 10vmin;
  }
  .Header-anim {
    animation: App-logo-spin infinite 20s linear;
  }
  h1 {
    font-size: 5vmin;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
