.Dashboard {
  max-width: 1400px;
  padding: 2px 10px;
  margin: 0 auto;
  text-align: left;
  h2 {
    font-size: 1.5rem;
    a {
      text-decoration: none;
      color: black;
      &:hover {
        color: rgb(48, 28, 2);
        text-shadow: 2px 2px 2px gray;
      }
      &:active {
        text-shadow: 1px 1px 1px gray;
      }
    }
  }
  .ChooseMentor {
    max-width: 300px;
  }
}
